.footer {
  padding: 17px 0;
  border-top: 1px solid #4d4b50;
  text-align: center;
}

.select-deselect-all-btn {
  float: left;
  margin-right: 8px;
  width: 24px;
  height: 24px;
  border: 2px solid #4d4b50;
  color: #4d4b50;
  border-radius: 50%;
  cursor: pointer;
}

.selected-photos-container {
  float: left;
}

.selected-photos {
  background: #8ec14a;
  display: inline-block;
  margin-right: 8px;
  width: 28px;
  height: 28px;
  color: white;
  border-radius: 50%;
  line-height: 28px;
  text-align: center;
  font-weight: bold;
}

.remove-selected-btn {
  display: inline-block;
  cursor: pointer;
  width: 28px;
  height: 28px;
  text-align: center;
}


