.pagination {
  text-align: center;
}


.pagination-page {
  display: inline-block;
  margin-right: 24px;
  background: #d8d8d8;
  color: #4b4b4b;
  width: 32px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  cursor: pointer;
  font-weight: bold;
}

.pagination-page:last-child {
  margin-right: 0;
}

.active-page {
  cursor: default;
  color: #d8d8d8;
  background: #4b4b4b;
}


