.app {
  min-height: calc(100vh - 64px - 48px);
  background: #fafafa;
  position: relative;
  padding-top: 48px;
}

.container {
  width: 1230px;
  margin: 0 auto;
}

.photos:after {
  content: "";
  display: table;
  clear: both;
}

.photos-count {
  text-transform: uppercase;
  margin-bottom: 36px;
}



