.card {
  -webkit-box-shadow: 0 2px 4px -2px;
          box-shadow: 0 2px 4px -2px;
  -webkit-transition: -webkit-box-shadow 300ms;
  transition: -webkit-box-shadow 300ms;
  -o-transition: box-shadow 300ms;
  transition: box-shadow 300ms;
  transition: box-shadow 300ms, -webkit-box-shadow 300ms;
  position: relative;
  padding-bottom: 65px;
  width: 390px;
  height: 260px;
  margin: 0 30px 30px 0;
  float: left;
}

.card:nth-child(3n) {
  margin-right: 0;
}

.card:hover {
  -webkit-box-shadow: 0 4px 30px -4px;
          box-shadow: 0 4px 30px -4px;
}

.card-img {
  position: relative;
  height: 100%;
  background-size: cover;
  background-color: gray;
  background-position: center;
}

.card:hover .checkbox,
.card:hover .overlay,
.card:hover .delete {
  display: block;
}

.overlay {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: .5;
  background: black;
}

.checkbox {
  display: none;
  width: 24px;
  height: 24px;
  border: 2px solid white;
  position: absolute;
  top: 15px;
  left: 25px;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: all 150ms;
  -o-transition: all 150ms;
  transition: all 150ms;
}

.checkbox-done {
  display: none;
  -webkit-transition: all 150ms;
  -o-transition: all 150ms;
  transition: all 150ms;
}

.checked .overlay,
.checked .checkbox-done {
  display: block;
}

.checked .checkbox {
  background: #8ec14a;
  border-color: #8ec14a;
  display: block;
}

.delete {
  display: none;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 15px;
  right: 25px;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: all 150ms;
  -o-transition: all 150ms;
  transition: all 150ms;
}




